export const propertyFiles = (property = {}, loan = {}) => ({
  auction: [
    {
      id: 'plans',
    },
    {
      id: 'cubage',
      doubleTooltip: true,
      condition: property.style === 'villa',
    },
    {
      id: 'pictures',
    },
    {
      id: 'marketingBrochure',
      condition: !!(
        loan &&
        loan.general &&
        loan.general.purchaseType === PURCHASE_TYPE.ACQUISITION
      ),
      required: false,
    },
  ],
  contract: [
    {
      id: 'rent',
      condition:
        !!loan.general && loan.general.usageType === USAGE_TYPE.INVESTMENT,
      doubleTooltip: true,
    },
    {
      id: 'landRegisterExtract',
      doubleTooltip: true,
    },
  ],
  all() {
    return [...this.auction, ...this.contract];
  },
});

export const getFileIDs = (list) => {
  let files;
  const ids = [];
  switch (list) {
  case 'borrower':
    files = borrowerFiles();
    break;
  case 'loan':
    files = loanFiles();
    break;
  case 'property':
    files = propertyFiles();
    break;
  default:
    throw new Error('invalid file list');
  }

  files.all().forEach(f => ids.push(f.id));

  return ids;
};

// Schema used for every file
export const FileSchema = new SimpleSchema({
  name: String,
  initialName: String,
  size: Number,
  type: String,
  url: {
    type: String,
    regEx: SimpleSchema.RegEx.Url,
  },
  key: String,
  fileCount: Number,
  status: {
    type: String,
    allowedValues: Object.values(FILE_STATUS),
  },
  error: { optional: true, type: String },
});

// Generates a schema given a list name (loan, or borrowers)
export const getFileSchema = (list) => {
  const schema = {};

  const arr = getFileIDs(list);

  arr.forEach((id) => {
    schema[id] = {
      type: Array,
      optional: true,
      maxCount: 100,
    };
    schema[`${id}.$`] = FileSchema;
  });

  return schema;
};

